.logo {
  display: inline-flex;
  align-items: flex-start;
}

.main-title {
  font-weight: 700;
  font-size: 72px;
  line-height: 1.3;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  margin-bottom: 24px;
  color: var(--gray-900);

  &--home{
    color: var(--white);
  }

  span{
    color: var(--primary);
  }
}

.main-descr {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: var(--gray-700);

  &--home{
    color: var(--white);
  }
}

.btn {
  font-weight: 700;
  font-size: 16px;
  line-height: 1;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  text-align: center;

  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  border-radius: 4px;
  padding: 18px 39px;
  color: var(--white);
}
.btn--stroke {
  border: 1px solid var(--white);
}

.btn-stroke--primary {
  border: 1px solid var(--primary);
  color: var(--primary);
}
.btn--fill-light {
}
.btn--fill {
  background: var(--primary);
}

.title {
  font-weight: 700;
  font-size: 46px;
  line-height: 130%;
  color: var(--gray-900);
  margin-bottom: 24px;
  &--center {
    text-align: center;
  }

}
.descr {
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  text-align: center;
  color: var(--gray-700);
  margin-bottom: 60px;
  &--center {
    text-align: center;
  }
}

.section-offset{
  padding-top: 120px;
}

.subtitle{
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: var(--gray-900);
  &--center {
    text-align: center;
  }
}

.sub-descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--gray-700);
  &--center {
    text-align: center;
  }
}

.subtitle-big {
  font-weight: 700;
  font-size: 28px;
  line-height: 1;
  color: var(--gray-900);
  &--center {
    text-align: center;
  }
}

.center-flex-button{
  @include flex-all-center;
  gap: 40px;
  margin-top: 80px;
}

.grid{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}

.centered {
  width: 100%;
  text-align: center;
}

